@import '_colorSetup.scss';

#footer {
    padding: 50px 0;
    position: absolute;
    background-color: rgb(53, 53, 53);
    color: white;
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
    text-align: center;

    span {
        padding: 10px;
        
        a{
            color: white;
            text-decoration: none;   
        }

        a:hover{
            color: $lightest-mint-shade;
        }
    }
}